@import "styles/statement";

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
/*导入字体*/
@font-face
{
  font-family: sourceHanSans-Bold;
  src: url('./styles/fontFamily/SourceHanSansSC-Normal.otf')
}
/*不允许右滑动*/
body {
  overflow-x: hidden;
}

/*清除li样式*/
li {
  list-style-type: none;
}

/*按钮样式*/
.myButton {
  width: 200px;
  height: 50px;
  font-size: 16px;
  border-radius: 10px;
  background-color: azure;
  color: black;
  border: 1px;
  transition-duration: 0.4s;
}

.myButton:hover {
  background-color: blue;
  color: white;
}

.myAnimate {
  transition:max-height 0.5s;
  overflow: hidden;


  &.show {
      max-height: 600px;
  }

  &.hidden {
    max-height: 0;
  }
}

